.menu {
    display: none;
}
.menu__item {
}
.active {
}

@media (min-width: 1024px){
    .menu{
       position: fixed; 
       display: block;
       right: 32px;
       top: 50%;
       transform: translateY(-50%);
       list-style: none;
       margin: 0;
       padding: 0;
       z-index: 100;
       counter-reset: counter;
    }
    .menu__item{
        position: relative;
        margin-bottom: 14px;

        &::before{
            counter-increment: counter;
            content: "0"counter(counter);
            position: absolute;
            left: -20px;
            top: 50%;
            transform: translateY(-50%);
            color: $white;
            font-size: px-to-rem(12px);
            line-height: px-to-rem(14px);
            opacity: 0;
        }
        a{
            width: 8px;
            height: 8px;
            border: 1px solid $primary;
            border-radius: 50%;
            display: block;
        }
        &:last-child{
            margin-bottom: 0;
        }
    }
    .menu__item.active{
        &::before{
            opacity: 1;
        }
        a{
            background-color: $white;
            width: 4px;
            height: 4px;
            margin: 0 auto;
            border: none;
        }
    }
}