.calc {
}
.calc__group {
    padding: 0;
    border: none;
    margin: 20px 0;
}
.calc__title {
    font-size: px-to-rem(14px); 
    line-height: px-to-rem(16px);
    font-weight: 700;
    margin-bottom: 10px;

}
.calc__wrap {
}
.calc__radio {
    margin: 7px 0;
    input[type="radio"]{
        display: none;
        &:checked{
            + label{
                &::before{
                    content: "";
                    width: 8px;
                    height: 3px;
                    border-left: 2px solid $primary;
                    border-bottom: 2px solid $primary;
                    position: absolute;
                    left: 3px;
                    top: 8px;
                    transform: rotate(-45deg);
                    z-index: 1;
                }
            }
        }
    }
    label{
       font-size: px-to-rem(17px); 
       line-height: px-to-rem(24px);
       position: relative;
       padding-left: 25px;
       cursor: pointer;
       display: inline-block;
       &::after{
           content: "";
           width: 16px;
           height: 16px;
           border: 1px solid $primary;
           box-sizing: border-box;
           background-color: transparent;
           border-radius: 5px;
           position: absolute;
           left: 0;
           top: 50%;
           transform: translateY(-50%);
       }
    }
}
.calc__input {
}
.calc__square {
    border: 1px solid rgba(255, 255, 255, 0.2);
    // box-sizing: border-box;
    border-radius: 5px;
    display: block;
    background-color: transparent;
    width: 100%;
    color: $white;
    text-align: center;
    font-size: px-to-rem(14px); 
    line-height: px-to-rem(16px);
    padding: 12px 0;
    margin-top: 10px;
}
.calc__range {
    display: none;
    label{
       @include visually-hidden; 
    }
}
.calc__wrap {
}
.calc__btn {
    @include btn
}
.calc__result {
    text-align: center;
    font-size: px-to-rem(17px); 
    line-height: px-to-rem(24px);
    margin: 20px 0;
}
@media (min-width: 480px){
    .calc__wrap{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }
    .calc__title, 
    .calc__input{
        flex-basis: 100%;
        width: 100%;
    }
    .calc__radio{
        margin-right: 15px;
        &:last-child{
            margin-right: 0;
        }
    }
    .calc__btn{
        flex-basis: 164px;
        width: 164px;
        margin-right: 25px;
    }
    .calc__result{
        text-align: left;
        font-size: px-to-rem(14px); 
        line-height: px-to-rem(16px);
    }
}
@media (min-width: 1024px){
    .calc{
        background-color: $black;
        border-radius: 5px;
    }
    .calc__input{
        display: none;
    }
    .calc__range{
        display: flex;
        align-items: center;
        flex-basis: 100%;
        margin: 7px 0;
        output{
            border: 1px solid rgba(255, 255, 255, 0.2);
            border-radius: 5px;
            display: block;
            background-color: transparent;
            width: 67px;
            color: $white;
            text-align: center;
            font-size: px-to-rem(14px); 
            line-height: px-to-rem(16px);
            padding: 3px 0;
            margin-top: 0;
            margin-right: 24px;
        }
    }
    input[type="range"]{
        width: 280px;
        flex-basis: 280px;
        margin: 10px 0;
        appearance: none;
        &::-webkit-slider-runnable-track{
            background-color: $white;
            width: 100%;
            height: 4px;
            cursor: pointer;
            border-radius: 10px;
        }
        &::-moz-range-track{
            background-color: $white;
            width: 100%;
            height: 4px;
            cursor: pointer;
            border-radius: 10px;
        }
        &::-ms-track{
            background-color: $white;
            width: 100%;
            height: 4px;
            cursor: pointer;
            border-radius: 10px;
        }   
        &::-webkit-slider-thumb{
           width: 8px;
           height: 8px;
           background-color: $primary;
           border-radius: 8px;
           cursor: pointer;
           appearance: none;
           margin-top: -2px;
        }
    }
}